import Axios from 'axios';
// ,  {msgAlert: false}
export default {
  classList() {
    return Axios.get('/api/v1/classes')
  },
  addClass(params) {
    return Axios.post('/api/v1/classes', params)
  },
  closeClass(id) {
    return Axios.put(`/api/v1/classes/${id}/close`)
  },
};



