import moment from "moment";

const Library = {
  // #
  getQuery(name) {
    // @params: name => eg: www.abc.com?id=2&type=museum; getQueryFn('type')
    let reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
    let r = window.location.search.substr(1).match(reg);
    if (r != null) {
      return decodeURIComponent(r[2]);
    }
    return null;
  },
  // 
  throttle(method, context, time = 500, arg) {
    clearTimeout(method.tId);
    method.tId = setTimeout(function () {
      method.call(context, arg);
    }, time)
  },
  // 
  typeValidate(str, type) {
    // @params: str => , type => email/idCard/phone/number/postalcode
    let email = /^[\w|.]+[@]\w+[.][\w.]+$/;
    let idCard = /(^\d{15}$)|(^\d{17}([0-9]|X|x)$)/;
    let phone = /^1[3|4|5|6|7|8|9]\d{9}$/;
    let postalcode = /^\d{6}$/;
    let chinese = /[^\a-zA-Z\u4E00-\u9FA5]/g; // /
    let number = /\D/g;
    let a_num = /\W/g; //  [^A-Za-z0-9_]
    // ams 
    let password = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,30}$/; // 630
    let merchant_name = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,50}$/; //  => 150///
    let description = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,100}$/; //  => 1100///
    // *** (2-30)
    let real_name = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,30}$/; //  => 130///
    let login_name = /^(?![0-9]+$)[0-9A-Za-z@._]{1,30}$/; //  => 150///@/.
    let device_name = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,50}$/; // => 150///
    let person_manage_name = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,50}$/; //  => 150///
    let monitor_task_name = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,20}$/; //  => 120///
    let osd = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,20}$/; //OSD => 120///
    let GBosd = /^[\u4e00-\u9fa5_0-9A-Za-z]{1,15}$/; //OSD => 120///

    let bool = false;
    switch (type) {
      case 'email':
        bool = email.test(str);
        break;
      case 'idCard':
        bool = idCard.test(str);
        break;
      case 'phone':
        bool = phone.test(str);
        break;
      case 'postalcode':
        bool = postalcode.test(str);
        break;
      case 'chinese':
        bool = !chinese.test(str);
        break;
      case 'number':
        bool = !number.test(str);
        break;
      case 'password':
        bool = password.test(str);
        break;
      case 'merchant_name':
        bool = merchant_name.test(str);
        break;
      case 'real_name':
        bool = real_name.test(str);
        break;
      case 'login_name':
        bool = login_name.test(str);
        break;
      case 'device_name':
        bool = device_name.test(str);
        break;
      case 'person_manage_name':
        bool = person_manage_name.test(str);
        break;
      case 'monitor_task_name':
        bool = monitor_task_name.test(str);
        break;
      case 'description':
        bool = description.test(str);
        break;
      case 'osd':
        bool = osd.test(str);
        break;
      case 'GBosd':
        bool = GBosd.test(str);
        break;
      default:
        alert('');
    }
    return bool; //@return: true => , false => 
  },
  //  >  
  timeToYMDHMS(time, type) {
    // @params: time => ms
    let timeTranslate = function (num) {
      return num < 10 ? `0${num}` : num
    };
    let date = new Date(parseInt(time));
    let y = 1900 + date.getYear();
    let m = timeTranslate(date.getMonth() + 1);
    let d = timeTranslate(date.getDate());
    let h = timeTranslate(date.getHours());
    let min = timeTranslate(date.getMinutes());
    let s = timeTranslate(date.getSeconds());
    let str = '';
    switch (type) {
      case 'YY-MM-DD':
        str = `${y}-${m}-${d}`;
        break;
      case 'YY-MM-DD H:M:S':
        str = `${y}-${m}-${d} ${h}:${min}:${s}`;
        break;
      case 'H:M:S':
        str = `${h}:${min}:${s}`;
        break;
      case 'objDate':
        str = { y, m, d, h, min, s };
        break;
      default:
        str = `${y}-${m}-${d} ${h}:${min}:${s}`;
        break;
    }
    return str;
  },
  // 
  //
  enterFullScreen() {
    var ele = document.documentElement;
    if (ele.requestFullscreen) {
      ele.requestFullscreen();
    } else if (ele.mozRequestFullScreen) {
      ele.mozRequestFullScreen();
    } else if (ele.webkitRequestFullScreen) {
      ele.webkitRequestFullScreen();
    }
  },
  //
  exitFullscreen() {
    if (document.exitFullscreen) {
      document.exitFullscreen();
    } else if (document.mozCancelFullScreen) { /* Firefox */
      document.mozCancelFullScreen();
    } else if (document.webkitExitFullscreen) { /* Chrome, Safari and Opera */
      document.webkitExitFullscreen();
    } else if (document.msExitFullscreen) { /* IE/Edge */
      document.msExitFullscreen();
    }
  },
  // 
  launchFullscreen(element) {
    if (element.requestFullscreen) {
      element.requestFullscreen();
    } else if (element.mozRequestFullScreen) {
      element.mozRequestFullScreen();
    } else if (element.webkitRequestFullscreen) {
      element.webkitRequestFullscreen();
    } else if (element.msRequestFullscreen) {
      element.msRequestFullscreen();
    }
  },
  // 
  isFullScreen() {
    return document.isFullScreen || document.mozIsFullScreen || document.webkitIsFullScreen;
  },
  // arrary => object (eg: [label:'', value: 1] => {1: ''})lable & value
  arraryToObject(arrary, key) {
    let Obj = {};
    for (let i = 0; i < arrary.length; i++) {
      let item = arrary[i];
      Obj[item.value] = item.label;
    }
    return Obj[key];
  },
  // n
  getDate(n) {
    // @params: n => n 
    let baseMonthsDay = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31]; //(2)
    let currentDate = new Date();
    let y = currentDate.getFullYear();
    let m = currentDate.getMonth() + 1;
    let d = currentDate.getDate();
    //28/29
    function isRunYear(fullYear) {
      return (fullYear % 4 == 0 && (fullYear % 100 != 0 || fullYear % 400 == 0));
    };
    // n 
    let later_y = y;
    let later_m = (m + n) % 12;
    if (m + n > 12) {
      later_y = later_y + 1;
    }
    let later_d = d;
    // 
    if (later_m === 2 && isRunYear(later_y)) {
      baseMonthsDay[1] = 29;
    }
    //  28 n 12-31 03-30
    if (later_d > 28) {
      later_d = baseMonthsDay[later_m - 1];
    }
    return `${later_y}-${later_m}-${later_d}`;
  },
  //flash By hq  
  flashChecker() {
    var hasFlash = false; // flash
    var isIE = /*@cc_on!@*/ 0; //IE
    if (isIE) {
      var swf = new ActiveXObject('ShockwaveFlash.ShockwaveFlash');
      if (swf) {
        hasFlash = true;
      }
    } else {
      if (navigator.plugins && navigator.plugins.length > 0) {
        var swf = navigator.plugins["Shockwave Flash"];
        if (swf) {
          hasFlash = true;
        }
      }
    }
    return hasFlash;
  },
  // ID
  createRandomId() {
    return (Math.random() * 10000000).toString(16).substr(0, 4) + '-' + (new Date()).getTime() + '-' + parseInt(Math.random() * 10000000).toString(16).substr(0, 4);
  },
  // 
  timeFormat(type) {
    let timeTranslate = function (num) {
      return num < 10 ? `0${num}` : num
    };
    let date = new Date();
    let y = date.getFullYear();
    let m = timeTranslate(date.getMonth() + 1);
    let d = timeTranslate(date.getDate());
    let str = '';
    switch (type) {
      case 'yyyy-mm-dd':
        str = `${y}-${m}-${d}`;
        break;
      case 'mm-dd-yyyy':
        str = `${m}-${d}-${y}`;
        break;
      case 'dd-mm-yyyy':
        str = `${d}-${m}-${y}`;
        break;
      case 'yyyymmdd':
        str = `${y}${m}${d}`;
        break;
      case 'mmddyyyy':
        str = `${m}${d}${y}`;
        break;
      case 'ddmmyyyy':
        str = `${d}${m}${y}`;
        break;
    }
    return str;
  },

  //  html  dom
  parseToDOM(str) {
    let divDom = document.createElement("div");
    if (typeof str == "string") {
      divDom.innerHTML = str;
    }
    return divDom.children[0]; // 
  },
  // 
  deepClone(obj) {
    if (obj === null || typeof obj !== 'object') return obj;
    var cpObj = obj instanceof Array ? [] : {};
    for (var key in obj) cpObj[key] = this.deepClone(obj[key]);
    return cpObj;
  },
  // ()
  judgeArraryEqual(arr1, arr2) {
    let array1 = JSON.parse(JSON.stringify(arr1)).sort();
    let array2 = JSON.parse(JSON.stringify(arr2)).sort();
    return JSON.stringify(array1) == JSON.stringify(array2);
  },
  // now  days  00:00:00
  getLatestDayStartedFromNow(days, now) {
    let latestDayFromNow = moment(now).subtract(days, 'day').format('x');
    let theDayStarted = moment(moment(Number(latestDayFromNow)).format('YYYY-MM-DD 00:00:00')).format('x');
    return Number(theDayStarted);
  },
  //  eg:  ["","",""] => {: 2, : 1}
  getRepeatNum(arr) {
    return arr.reduce(function (prev, next) {
      prev[next] = (prev[next] + 1) || 1;
      return prev;
    }, {});
  },
  // 01:00:00 ms
  timeToMs(time) {
    let ms = '';
    let hour = time.split(':')[0];
    let minutes = time.split(':')[1];
    let second = time.split(':')[2];
    ms = (Number(hour * 3600) + Number(minutes * 60) + Number(second)) * 1000;
    return ms;
  },
  // ms  
  msToTime(ms) {
    let time = '';
    ms = ms / 1000;

    if (ms > -1) {
      let hour = Math.floor(ms / 3600);
      let minutes = Math.floor(ms / 60) % 60;
      let second = ms % 60;

      time = hour < 10 ? '0' + hour + ':' : hour + ':';
      time += minutes < 10 ? '0' + minutes + ':' : minutes + ':';
      time += second < 10 ? '0' + second : second;
      return time;
    }
  },
  //Unix
  timeToUnix(val) {
    val = val + ":00:00:00:00:00:00"
    let array = val.split(/-|:/)
    array = array.map(item => +item)
    return parseInt(new Date(array[0], array[1] - 1, array[2], array[3], array[4], array[5]).getTime() / 1000);
  },
  //0
  PrefixZero(num, n) {
    return (Array(n).join(0) + num).slice(-n);
  },
  //array,ms[]
  weekRange(starttime, endtime) {
    let arr = [false, false, false, false, false, false, false];
    let index;
    starttime = new Date(new Date(starttime).setHours(0, 0, 0, 0)).getTime()
    console.dir(starttime)
    for (let i = 0; i < 7; i++) {
      let time = starttime + i * 1000 * 60 * 60 * 24
      if (time >= endtime) {
        break;
      }
      index = new Date(time).getDay()
      console.dir(time)
      console.dir(index)
      arr[index] = true
    }
    let Sunday = arr.shift();
    arr.push(Sunday)
    console.dir(arr)
    return arr
  },
  //  HH:mm:ssDate
  formatSecond(value) {
    let secondTime = parseInt(value);
    let minuteTime = 0;
    let hourTime = 0;
    if (secondTime > 60) {
      minuteTime = parseInt(secondTime / 60);
      secondTime = parseInt(secondTime % 60);
      if (minuteTime > 60) {
        hourTime = parseInt(minuteTime / 60);
        minuteTime = parseInt(minuteTime % 60);
      }
    }
    let d = new Date();
    d.setHours(hourTime, minuteTime, secondTime)
    return d;
  },
  //
  calcItemWidth(width, min) {
    // 
    if (width > 0) {
      const maxColumn = parseInt(String(width / min), 10);
      //  
      return width / maxColumn;
    } else {
      return min;
    }
  },
  //
  preloadimg(urls, limit = 3) {
    console.log('preloadimg',urls)
    let count = 0;
    function loadImg(url) {
      return new Promise((resolve, reject) => {
        let img = new Image();
        img.onload = function () {
          resolve(img);//resolve
        }
        img.onerror = function () {
          reject(url + '');//reject
        }
        img.src = url;
      })
    }
    //
    function bao() {
      count++;
      console.log(":", count)
      //urls0
      if (urls.length > 0 && count <= limit) {
        //shift
        loadImg(urls.shift()).then(() => {
          //
          count--
          //
        }).then(bao)
      }
    }
    function async() {
      //
      for (let i = 0; i < limit; i++) {
        bao();
      }
    }
    async()
  }
};

export default Library;


